<template>
  <div class="text-editor">
    <text-editor
      :default-value="textValue"
      :maxLength="600"
      placeholder="请输入内容"
      :textarea-height="200"
      @input="handlerInput"
      @change="handlerChange"
    ></text-editor>
  </div>
</template>

<script>
  import { TextEditor } from '@handday/components'
  export default {
    name: 'textEditorView',
    components: {
      TextEditor
    },
    data () {
      return {
        textValue: {
          value: '纯文本编辑器示例',
          // 1-加粗，2-斜体，4-下划线，8-左对齐，16-居中对齐，32-右对齐
          textStyles: [ 1, 2, 4, 16]
        }
      }
    },
    methods: {
      /**
       * @description: 输入事件触发
       * @param {Object} val
       * @return {*}
       */
      handlerInput (val) {
        console.log(val)
      },
      /**
       * @description: 文本域失去焦点触发
       * @param {Object} val
       * @return {*}
       */
      handlerChange (val) {
        console.log(val)
      }
    }
  }
</script>

